import React, { Component } from 'react';
import './BillingInfo.css'
import payment from '../assets/BillingPage.png'
import card from '../assets/Capture.PNG'
import { Link } from "react-router-dom";
import { Form, Button } from "react-bootstrap";
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Cookies from "js-cookie";
import { apiCall } from "../Services/Services";


class Pricingplan extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        month: "",
        year: "",
        name: "",
        mobileNumber: "",
        cardNumber: "",
        cvv: "",
        expiry: "",
        step: 1,
        iserror: false,
        errormsg: "no error",
        isloading: false
    };
    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if(user_details === undefined || user_details == ""){
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);
    }
    routeChange = () => {
        let path = `/AccountCreated`;
        this.props.history.push(path);
    }
    handleChange = (event) => {
        if (event.target.type === "number") {
            const cardNumber = event.target.value;
            this.setState({ cardNumber: cardNumber });
        } else if (event.target.type === "number") {
            const cvv = event.target.value;
            this.setState({ cvv: cvv });
        } else if (event.target.type === "month") {
            const expiry = event.target.value;
            var date = expiry.split('-');
            var month = date[1];
            var year = date[0].substr(2);
            this.setState({ expiry: expiry, month: month, year: year });
        }
    };

    CustomerApi = (token, details) => {
        var user_details = Cookies.get("user_details");
        user_details = JSON.parse(user_details);
        const customer = {
            email: user_details.email,
            source: token,
            description: "customer for WiseBiz app"
        };
        fetch('https://api.stripe.com/v1/customers', {
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/x-www-form-urlencoded',
                Authorization: `Bearer sk_test_51HR7NXI7mDOSnQEtNfQEBoPrKDOrCfcjn8JtW5vfipLEpdlSNz4JJcGt7WwIqIpk6ZnxlJS2psG5bLfHUwfbPz0o00xIezbZZX`
            },
            method: 'post',
            body: Object.keys(customer)
                .map(key => key + '=' + customer[key])
                .join('&')
        }).then(response =>
            response.json())
            .then(JsonData => {
                console.log("Response from Stripe customer", JsonData);

                if (JsonData.error) {
                    this.setState({ isloading: false })
                    alert(JsonData.error.message)
                    return
                }
                var detail = details;
                detail.card_wallet_id = JsonData.id;
                this.registeTokenApi(details)
                console.log("customer id from stripe", JsonData.id);

            }).catch(error => {
                this.setState({ isloading: false })
                alert(error)
            });
    };

    
    registeTokenApi = (details) => {
        
        var user_registered = Cookies.get("user_registered");
        user_registered = JSON.parse(user_registered);
        console.log('herer');
        const urlString = 'https://api.socard.io/api/auth/updatePaymentToken';
        var formBody = [];
        for (var property in details) {
            var encodedKey = encodeURIComponent(property);
            var encodedValue = encodeURIComponent(details[property]);
            formBody.push(encodedKey + '=' + encodedValue);
        }
        formBody = formBody.join('&');
        console.log(formBody);
        fetch(urlString, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
                'Authorization': 'Bearer ' + user_registered.token,

            },
            body: formBody
        })
            .then(response => response.text())
            .then(responseJson => {
                console.log('card entry .............');
                console.log(responseJson);
                var JsonData = JSON.parse(responseJson);
                var success = JsonData.status;
                if (success) {
                    var user_details = Cookies.get("user_details");
                    user_details = JSON.parse(user_details);

                    var action = 'subscription';
                    var body = JSON.stringify({
                        user_id: user_details.id === undefined ?  user_details.user.id : user_details.id,
                        status: 1
                    });
                    apiCall(action, body, {
                            "Content-Type": "application/json",
                            "X-Requested-With": "XMLHttpRequest",
                            'Authorization': 'Bearer ' + this.state.userData.user.token,
                        }).then((result) => {
                        console.log("subscription", result)
                        let responseJSON = result;
                        if (responseJSON.error) {
                            this.setState({
                                iserror: true,
                                errormsg: responseJSON.message,
                            });
                        } else {
                            this.setState({ isloading: false });
                            this.props.history.push("/AccountCreated");
                            // if (responseJSON.data) {
                            //     console.log("dataa", responseJSON.data);
                            //     let path = `/user/PricingPlan`;
                            //     this.props.history.push(path);
                            // }
                        }
                        
                        this.setState({ isloading: false });
                    });
                    this.setState({ isloading: false });
                }
                else {
                    console.log('JsonData.success ', JsonData.message)
                    /*this.setState({
                      dialogMessage: JsonData.message,
                      dialogVisible: true,
                    });*/
                    this.setState({
                        isloading: false
                    });
                    alert("  " + JsonData.message);
                }
            })
            .catch(error => {
                console.log('Internal server error detected' + error);
                console.error(error);
            });
    };

    complete = () => {        
        this.setState({ isloading: true });
        var user_details = Cookies.get("user_details");
        user_details = JSON.parse(user_details);
        var user_registered = Cookies.get("user_registered");
        // user_registered = JSON.parse(user_registered);
        console.log('details', this.state);
        console.log('user_registered', user_registered);
        console.log('user_details', user_details);
        const card = {
            'card[number]': this.state.cardNumber,
            'card[exp_month]': this.state.month,
            'card[exp_year]': this.state.year,
            'card[cvc]': this.state.cvv
        };
        fetch('https://api.stripe.com/v1/tokens', {
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/x-www-form-urlencoded',
                Authorization: `Bearer pk_test_51HR7NXI7mDOSnQEtcwFw6DhImUtwu5iIHSVB58vZlVMMiJhnuJRDb2kXS3mbWX20Caq65TmV9KmUWlU7w32U8PaQ00hEhOiDhZ`
            },
            method: 'post',
            body: Object.keys(card)
                .map(key => key + '=' + card[key])
                .join('&')
        }).then(response =>
            response.json())
            .then(JsonData => {
                console.log("Response from Stripe token", JsonData);

                if (JsonData.error) {
                    this.setState({ isloading: false })
                    alert(JsonData.error.message)
                    return
                }
                console.log("Response from Stripe last 4", JsonData.card.last4);
                console.log("Response from Stripe brand", JsonData.card.brand);
                console.log("Response from Stripe funding", JsonData.card.funding);
                var details = {
                    user_id: user_details.id,
                    card_wallet_id: JsonData.id,
                    type: JsonData.card.funding,
                    brand: JsonData.card.brand,
                    last_four_digit: JsonData.card.last4,
                };
                this.CustomerApi(JsonData.id, details)
                // this.registeTokenApi(details);
                
                this.setState({ isloading: true });

            }).catch(error => {
                this.setState({ isloading: false })
                alert(error)
            });
        
    };
    
    render() {
        const { iserror, errormsg, isloading } = this.state;
        return (
            <div className="container">
                <div className="row  mt-5 ml-3 mr-3">
                    <div className="col-lg-4 col-sm-12"></div>
                    <div className="col-lg-4 col-sm-12">
                        <div className="row mt-2">
                            <div className="col-3">
                                <img src={card} style={{ width: 60 }} />
                            </div>
                            <div className="col-3"></div>
                            <div className="col-6">
                                <img src={payment} style={{ width: 130 }} />
                            </div>

                        </div>
                        {/* <div className="container">
                                <div className="mt-5">
                                    <span style={{ fontSize: "18px", color: "gray" }}>Card number</span >
                                    <input style={{ width: "100%", borderRadius: "10px", borderColor: "gray" }} type="number" name="cardNumber" id="search" onChange={this.handleChange} />
                                </div>
                            </div> */}
                        <Form className="mt-5">
                            <Form.Group controlId="cardNumber">
                                <span style={{ fontSize: "14px", color: "gray" }}>Card number</span >
                                <Form.Control style={{ fontSize: "16px", borderRadius: "10px" }} type="number" required name="cardNumber" onChange={this.handleChange} />
                            </Form.Group>
                            {/* <Form.Group className="container" controlId="cvv"> */}
                            <div className="row container">
                                <span style={{ fontSize: "14px", color: "gray" }}>CVV</span >
                                <span style={{ fontSize: "14px", color: "gray", marginLeft: 95 }}>Expiry</span >
                            </div>
                            {/* </Form.Group> */}
                            <Form.Group className="container" controlId="expiry">
                                <div className="row">
                                    <Form.Control style={{ fontSize: "16px", borderRadius: "10px", width: "35%" }} type="name" required name="cvv" onChange={this.handleChange} />
                                    <Form.Control style={{ fontSize: "16px", borderRadius: "10px", width: "55%", marginLeft: 20 }} type="month" required name="expiry" onChange={this.handleChange} />
                                    {/* <Calendar format='DD/MM/YYYY' date='4-12-2014' /> */}
                                </div>
                            </Form.Group>
                        </Form>
                        {/* <div className="container">
                            <div className=" mt-2">
                                <span style={{ fontSize: "18px", color: "gray" }}>CVV</span >
                                <span style={{ fontSize: "18px", color: "gray", marginLeft: 90 }}>Expiry</span >
                            </div>
                            <div className=" mt-2 mb-5">
                                <input style={{ width: "40%", borderRadius: "10px", borderColor: "gray", }} type="text" name="CVV" onChange={this.handleChange} />
                                <input style={{ width: "50%", borderRadius: "10px", borderColor: "gray", marginLeft: 20 }} name="expiry" type="number" placeholder="mm/yy" onChange={this.handleChange} />
                            </div>
                        </div> */}
                        <div className="pt-5">
                            <p style={{ fontSize: "14px" }}>It will automactically upgrade to Essentials
                            Plan when you exceed 100 customer base.
                        Flexible, no contract. Unsubscribe anytime.</p>
                        </div>
                        {iserror ? <p className="text-danger text-center">{errormsg}</p> : ""}
                        {isloading ? (
                            <Loader className="text-center" type="Oval" color="#323A40" height={50}
                                width={50}
                            // timeout={3000} //3 secs
                            />
                        ) : (
                            <div className=" text-center buttons">
                                <button onClick={this.complete} className="btn " style={{ background: "#2101F0", color: "white", width: "100%", fontSize: "18px", borderRadius: "10px" }} >
                                    Complete
                                </button>
                            </div>
                        )}
                        
                    </div>
                    <div className="col-lg-4 col-sm-12"></div>
                </div>
            </div>
        )
    }
}
export default Pricingplan;