import React, { Component } from 'react';
import './SideNav.css'
import logo from '../assets/logo.png'

class SideNav extends Component {
    openNav = () => {
        document.getElementById("mySidenav").style.width = "250px";
    }
    closeNav = () => {
        document.getElementById("mySidenav").style.width = "0";
    }

    render() {
        return (
            <div>
                <div id="mySidenav" className="sidenav">
                    <a className="closebtn" onClick={() => this.closeNav()}>&times;</a>
                    <a href="/user/Account">Account</a>
                    <a href="/user/DownloadApp">Download app</a>
                    <a href="/user/Subscription">Subscription</a>
                    <a href="/user/BillingCardInfo">Billing</a>
                    <a href="/user/PricingPlan">Pricing Plan</a>
                    <a href="/user/logout">Logout</a>
                </div>
                <div className="">
                    <span className="ml-4" style={{ fontSize: 30, cursor: "pointer", color: "white" }} onClick={() => this.openNav()}>&#9776; </span>
                    <img className="" src={logo} style={{ width: 130, marginTop: -14 }} />
                </div>
            </div>
        )
    }


}

export default SideNav;
