import React, { Component } from 'react';
import round from '../assets/SoConnect_ROUNDED.png'
import appstore from '../assets/AppStore.png'
import Cookies from "js-cookie";

class AccountCreated extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if(user_details === undefined || user_details == ""){
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);
    }
    routeChange = () => {
        let path = `/user/Account`;
        this.props.history.push(path);
    }

    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-4 col-sm-12"></div>
                    <div className="col-lg-4 col-sm-12" style={{ padding: "4%" }}>
                        {/* <p style={{ fontWeight: "bold", paddingLeft: "5%", paddingTop: "5%", fontSize: "26px" }}>Download app</p> */}
                        <div className="ml-5 mr-5 mt-4">
                            <div className="row">
                                <div className="col-12">
                                    <h3 style={{ fontWeight: "bold", textAlign: "center" }}>Account Created</h3>
                                </div>
                            </div>
                            <div style={{ textAlign: "center" }}>
                                <div className="mt-5 mb-5">
                                    <p style={{ fontSize: "20px", textAlign: "center" }}>You’ve successfully created your account. Download SoConnect app and start access your account.</p>
                                </div>
                                <div style={{}}>
                                    <img src={round} width="40%" />
                                </div>
                                <div className="mt-3 mb-3">
                                    <p style={{ fontSize: "25px", fontWeight: "bold" }}>SoConnect</p>
                                </div>
                                <div style={{}}>
                                    <img src={appstore} width="100%" />
                                </div>
                            </div>
                            <div className="row mt-5">
                                <p style={{ fontSize: "16px", textAlign: "center" }}>
                                    You can manage your subscription
                                    and billing info in your account.
                                </p>
                            </div>
                            <div className="row">
                                <div className="col-12 text-center buttons">
                                    <button onClick={this.routeChange} className="btn " style={{ background: "#2101F0", color: "white", width: "100%" }} >
                                        Continue
                            </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-sm-12"></div>
                </div>
            </div>
        )
    }
}

export default AccountCreated;
