import React, { Component } from 'react';
import round from '../assets/SoConnect_ROUNDED.png'
import appstore from '../assets/AppStore.png'

class DownloadApp extends Component {

    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-4 col-sm-12"></div>
                    <div className="col-lg-4 col-sm-12" style={{ padding: "4%" }}>
                        {/* <p style={{ fontWeight: "bold", paddingLeft: "5%", paddingTop: "5%", fontSize: "26px" }}>Download app</p> */}
                        <div className="ml-5 mr-5 mt-4">
                            <div className="row">
                                <div className="col-12">
                                    <h3 style={{ fontWeight: "bold" }}>Download app</h3>
                                </div>
                            </div>
                            <div style={{ textAlign: "center" }}>
                                <div className="mt-5 mb-5">
                                    <p style={{ fontSize: "20px" }}>Install <span style={{ fontWeight: "bold" }}>SoConnect</span> app to access
                                    your account </p>
                                </div>
                                <div style={{}}>
                                    <img src={round} width="40%" />
                                </div>
                                <div className="mt-3 mb-3">
                                    <p style={{ fontSize: "25px", fontWeight: "bold" }}>SoConnect</p>
                                </div>
                                <div style={{}}>
                                    <img src={appstore} width="100%" />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-sm-12"></div>
                </div>
            </div>
        )
    }


}

export default DownloadApp;
